.profile {
  max-width: 880px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-start;
}

@media screen and (max-width: 917px) {
  .profile {
    margin: 0 19px;
  }
}

@media screen and (max-width: 767px) {
  .profile {
    flex-direction: column;
    align-items: center;
  }
}